/* Start Video Styles */
#generic-block .cms-section div.cms-video {
  grid-column-start: 5;
  grid-column-end: 23;
  --plyr-video-controls-background: transparent;
  --plyr-video-control-background-hover: var(--light-main);
  --plyr-video-control-color: var(--light-main);
  --plyr-video-control-color-hover: var(--dark-main);
  --plyr-range-thumb-height: 0px;
  --plyr-range-fill-background: var(--light-main);
}
#generic-block .cms-section div.cms-video .pure-g, #generic-block .cms-section div.cms-video .plyr {
  width: 100%;
}
#generic-block .plyr__controls {
  display: grid;
  grid-template-columns: auto auto 1fr auto auto;
  grid-gap: var(--s1);
}
#generic-block .plyr__controls button {
  margin-bottom: 0;
  margin-right: 0;
}
#generic-block .plyr__controls .plyr__time {
  letter-spacing: 0;
}
#generic-block .popup-video-link {
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
}
#generic-block .vid-popup {
  border: none;
  padding: 0;
  background: transparent;
  cursor: zoom-in;
  position: relative;
}
#generic-block .vid-popup img {
  width: 100%;
}
#generic-block .vid-popup i.fas {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  background: transparent;
  font-size: var(--s5);
  cursor: pointer;
  text-align: center;
}
#generic-block .vid-popup i.fas:before {
  color: var(--light-main);
  z-index: 99;
  position: relative;
  text-shadow: 0 0 7px var(--dark-main);
}
#generic-block .vid-popup i.fas:hover, #generic-block .vid-popup i.fas:active {
  transform: scale(0.95);
}
#generic-block .mfp-bg {
  background: #222;
  opacity: 1;
}
#generic-block .mfp-bg.video-plyr + .mfp-wrap {
  grid-column-start: 5;
  grid-column-end: 23;
  --plyr-video-controls-background: transparent;
  --plyr-video-control-background-hover: var(--light-main);
  --plyr-video-control-color: var(--light-main);
  --plyr-video-control-color-hover: var(--dark-main);
  --plyr-range-thumb-height: 0px;
  --plyr-range-fill-background: var(--light-main);
}
#generic-block .mfp-bg.video-plyr + .mfp-wrap .mfp-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
}
#generic-block .mfp-bg.video-plyr + .mfp-wrap .mfp-container .mfp-content {
  max-width: 80vw;
  order: 2;
}
#generic-block .mfp-bg.video-plyr + .mfp-wrap .mfp-container .mfp-content .vid-popup {
  display: flex;
  flex-direction: column;
}
#generic-block .mfp-bg.video-plyr + .mfp-wrap .mfp-container .mfp-content .vid-popup .plyr {
  order: 2;
  width: 100%;
  align-self: center;
  display: grid;
  grid-template-rows: 1fr auto;
  background: transparent;
}
#generic-block .mfp-bg.video-plyr + .mfp-wrap .mfp-container .mfp-content .vid-popup .plyr .plyr__controls {
  position: relative;
  grid-row: 2;
  display: grid;
  grid-template-columns: auto auto 1fr auto auto;
  grid-gap: var(--s1);
}
#generic-block .mfp-bg.video-plyr + .mfp-wrap .mfp-container .mfp-content .vid-popup .plyr .plyr__controls button {
  margin-bottom: 0;
  margin-right: 0;
}
#generic-block .mfp-bg.video-plyr + .mfp-wrap .mfp-container .mfp-content .vid-popup .plyr .plyr__controls .plyr__time {
  letter-spacing: 0;
}
#generic-block .mfp-bg.video-plyr + .mfp-wrap .mfp-container .mfp-content .vid-popup .mfp-close {
  order: 1;
  position: relative;
  align-self: end;
  color: white;
  font-size: 6rem;
  width: auto;
  height: auto;
  margin-bottom: 1rem;
  margin-right: 0;
}
#generic-block .mfp-bg.video-plyr + .mfp-wrap .mfp-container .mfp-content .vid-popup figcaption {
  order: 3;
  position: relative;
  background: transparent;
  text-align: center;
  margin: 3rem auto 0 auto;
}
#generic-block .mfp-bg.video-plyr + .mfp-wrap .mfp-container .mfp-content .vid-popup figcaption * {
  color: var(--light-main);
}
#generic-block .mfp-bg.video-plyr + .mfp-wrap .mfp-container .mfp-arrow {
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
#generic-block .mfp-bg.video-plyr + .mfp-wrap .mfp-container .mfp-arrow:after, #generic-block .mfp-bg.video-plyr + .mfp-wrap .mfp-container .mfp-arrow:before {
  color: var(--light-main);
  width: auto;
  top: unset;
  left: unset;
  right: unset;
  bottom: unset;
  position: relative;
  margin: 0;
  margin-top: -100%;
  font-size: var(--f8);
}
#generic-block .mfp-bg.video-plyr + .mfp-wrap .mfp-container .mfp-arrow.mfp-arrow-left {
  left: unset;
  right: unset;
  order: 1;
}
#generic-block .mfp-bg.video-plyr + .mfp-wrap .mfp-container .mfp-arrow.mfp-arrow-left:before {
  content: "";
  display: none;
}
#generic-block .mfp-bg.video-plyr + .mfp-wrap .mfp-container .mfp-arrow.mfp-arrow-left:after {
  content: "\f104";
  border: none;
}
#generic-block .mfp-bg.video-plyr + .mfp-wrap .mfp-container .mfp-arrow.mfp-arrow-right {
  left: unset;
  right: unset;
  order: 3;
}
#generic-block .mfp-bg.video-plyr + .mfp-wrap .mfp-container .mfp-arrow.mfp-arrow-right:before {
  content: "";
  display: none;
}
#generic-block .mfp-bg.video-plyr + .mfp-wrap .mfp-container .mfp-arrow.mfp-arrow-right:after {
  content: "\f105";
  border: none;
}

/* End Video Styles */